<template>
    <div class="anSwiper">
        <swiper :options="swiperOption">
            <swiper-slide v-for="slide in listStays" :key="slide.title">
              <img :src="slide.img" alt="">
              <!-- <div class="product-text">
                <p>{{ slide.title }}</p>
                <p>科技之所以叫科技因为我们要用它影响世界</p>
              </div> -->
              </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>
        </swiper>
    </div>
</template>

<script>
import { listStays } from "~/data/listStays.js";

export default {
  data() {
    return {
      swiperOption: {
        autoplay: 2000,
        setWrapperSize: true,
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true,
        loop: true,
        centeredSlides: true,
        spaceBetween: 2
      },
      listStays: listStays
    };
  },
  mounted() {}
};
</script>


<style  lang="scss" scoped>
.anSwiper {
  border-radius: 4px;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
  }
  .swiper-slide {
    color: #fff;
    width: 100%;
    height: 100%;
    .product-text {
      padding: 15px;
      height: 100%;
      background-color: #03a57b;
      box-sizing: border-box;
      p:nth-child(1) {
        font-size: 20px;
        margin-bottom: 5px;
      }
      p:nth-child(2) {
        font-size: 13px;
        font-weight: 400;
      }
    }
  }
}
</style>



